*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    
}
body{
    background-color: rgb(87, 149, 203);
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
.app-container{
    max-width: 400px;
    background-color: #fff;
    padding: 20px;
    border-radius: 5px;
}
.heading{
    text-align: center;
    font-size: 20px;
    font-weight: bold;
    margin-bottom: 20px;
    text-transform: uppercase;
    color: blue;
}
.input-group, .check-box, .generated-password{
    margin-bottom: 15px;
}
.check-box{
    display: flex;
    align-items: center;
}
.check-box input{
    margin-left: 15px;
}
.input-group label{
    font-weight: bold;
    display: block;
    font-size: 16px;
    margin-bottom: 10px;
}
.input-group input{
    width: 100%;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
}
.generate-btn{
    padding: 10px 20px;
    background-color: rgba(5, 5, 155, 0.97);
    border: none;
    border-radius: 5px;
    color: #fff;
    margin-bottom: 15px;
    cursor: pointer;
}
.generate-btn:hover{
    background-color: blue;
}
.generated-password{
    border: 1px solid #ccc;
    border-radius: 4px;
    display: flex;
    overflow: hidden;
}
.generated-password input{
    border: none;
    flex: 1;
    padding: 10px;
    outline: none;
}
.input-group input {
    outline: none;
}
.copy-btn{
    padding: 5px;
    background-color: green;
    border: none;
    color: #fff;
}